const API_KEY = "c7196d5ac3e7657c331b08ca81a85a9a";
const BASE_PATH = "https://api.themoviedb.org/3";
//https://api.themoviedb.org/3/movie/now_playing?api_key=c7196d5ac3e7657c331b08ca81a85a9a

interface IMovie {
    id: number;
    backdrop_path: string;
    poster_path: string;
    title: string;
    overview: string;
  }
  
  export interface IGetMoviesResult {
    dates: {
      maximum: string;
      minimum: string;
    };
    page: number;
    results: IMovie[];
    total_pages: number;
    total_results: number;
  }
  
export function getMovies() {
  return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then(
    (response) => response.json()
  );
}
